.reminder-wrap {
	position: relative;

	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		text-align: center;
		padding: 5rem 3rem;
		@include min-lg {
			width: 100%;
		}
		@include ipad {
			padding: 3rem 4rem;
		}
		@include max-sm {
			width: 100%;
			padding: 5rem 1.5rem;
		}
	}

	.btn-reminder {
		z-index: 3;
		position: relative;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin: 1.5rem auto 0;

		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
